import React from 'react'
import {Typography,Breadcrumbs,Link,Box,Grid} from '@mui/material'
import Pagination from '@mui/material/Pagination'
import FavoriteIcon from '@mui/icons-material/Favorite'
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import HomeIcon from '@mui/icons-material/Home';
import StorefrontIcon from '@mui/icons-material/Storefront';
import useFavorites from './useFavorites'
import CardX from '../../Components/Card/CardFavorites/CardX'

const Favorites = () => {
  const { state, handlers, context } = useFavorites()

  return (
    <Box sx={{ px: { xs: 0, md: '5%', lg: '10%' }}}>    
      <Box sx={{ px: 2, py: 5 }}>
        <Breadcrumbs aria-label="breadcrumb" separator={<NavigateNextIcon fontSize="small" sx={{ color: '#00004e' }} />}
          sx={{ mb: 2, padding: '8px 16px', borderRadius: 2, color: '#00004e', fontFamily: 'Orbitron, sans-serif' }}>
          <Link href="/home" underline="hover" sx={{ display: 'flex', alignItems: 'center', color: '#00004e' }}>
            <HomeIcon sx={{ mr: 0.5, color: '#00004e' }} fontSize="small" /> Inicio
          </Link>
          <Link href="/home/products" underline="hover" sx={{ display: 'flex', alignItems: 'center', color: '#00004e' }}>
            <StorefrontIcon sx={{ mr: 0.5, color: '#00004e' }} fontSize="small" /> Productos
          </Link>
          <Typography sx={{ display: 'flex', alignItems: 'center', fontWeight: 'bold', color: '#00004e' }}>
            <FavoriteIcon sx={{ mr: 0.5, color: '#00004e' }} fontSize="small" /> Favoritos
          </Typography>
        </Breadcrumbs>

        <Grid container>          
          {context.spliceProducts.map((product, index) => (
            <Grid item xs={12} md={6} lg={4} sx={{ pb: 2, pr: { xs: 0, md: 2, lg: 2 } }}>
              <CardX  product={product} />
            </Grid>
          ))}          
        </Grid> 

        <Box mt={4} display="flex" justifyContent="center">
            <Pagination count={Math.ceil(context.dataContext.length / state.itemsPerPage)}
              page={state.currentPage}
              onChange={handlers.handlePageChange}
              color="primary"
            />
        </Box>       
      </Box>
    </Box>
  )
}

export default Favorites
